import type { ReactNode } from 'react'
import './globals.css'

export default function RootLayout(props: RootLayoutProps) {
  const { children } = props

  return (
    <html lang="en">
      <body className="min-h-screen bg-dark text-mutedGrey antialiased">
        {children}
      </body>
    </html>
  )
}

interface RootLayoutProps {
  children: ReactNode
}

